"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import Link from "next/link"

export function ResultsDisplay() {
  const [isPlaying, setIsPlaying] = useState({
    vocals: false,
    instrumental: false
  })

  // In a real app, these URLs would come from your backend after processing
  const dummyTracks = {
    vocals: "/demo-vocals.mp3",
    instrumental: "/demo-instrumental.mp3"
  }

  const handlePlay = (track: 'vocals' | 'instrumental') => {
    const audioElement = document.getElementById(`${track}-audio`) as HTMLAudioElement
    if (audioElement) {
      if (isPlaying[track]) {
        audioElement.pause()
      } else {
        audioElement.play()
      }
      setIsPlaying(prev => ({
        ...prev,
        [track]: !prev[track]
      }))
    }
  }

  const TrackCard = ({ type, title }: { type: 'vocals' | 'instrumental', title: string }) => (
    <Card className="p-6 space-y-4">
      <h3 className="text-xl font-semibold">{title}</h3>
      
      <div className="w-full bg-gray-100 rounded-lg p-4">
        <audio
          id={`${type}-audio`}
          src={dummyTracks[type]}
          className="w-full"
          controls
          onPlay={() => setIsPlaying(prev => ({ ...prev, [type]: true }))}
          onPause={() => setIsPlaying(prev => ({ ...prev, [type]: false }))}
        />
      </div>

      <div className="flex justify-between items-center">
        <Button
          variant="outline"
          onClick={() => handlePlay(type)}
        >
          {isPlaying[type] ? 'Pause' : 'Play'}
        </Button>
        
        <Button
          className="bg-[#30D5C8] hover:bg-[#2bc0b4]"
          asChild
        >
          <Link href={dummyTracks[type]} download>
            Download
          </Link>
        </Button>
      </div>
    </Card>
  )

  return (
    <div className="space-y-8">
      <TrackCard type="vocals" title="Vocals" />
      <TrackCard type="instrumental" title="Instrumental" />
      
      <div className="flex justify-center pt-4">
        <Button
          variant="outline"
          asChild
        >
          <Link href="/upload">
            Process Another File
          </Link>
        </Button>
      </div>
    </div>
  )
}
